@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Oswald:wght@300;400;600&display=swap);
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

body{
	background: #000;
	font-family: 'Oswald', sans-serif;
	font-weight: 600;
	font-size: 16px;
}
button{
  width: 50%;
  max-width: 250px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 4vh;
  cursor: pointer;
  background: #ffe23b;
  border: none;
  border-bottom: solid 5px #d48b1d;
  font-weight: 600;
  font-size: 1.2em;
  border-radius: 10px;
  padding: 10px 0;
  box-sizing: border-box;
  transition: .2s;
}
button:active{
  transform: translateX(-50%) scale(.95);
}

.author{
  text-align: center;
  color: #FFF;
  pointer-events: none;
  position: absolute;
  bottom: 1.2vh;
  left: 50%;
  transform: translateX(-50%);
  font-size: .9em;
  font-weight: 400;
}
.author span{
  color: #ffe23b;
  font-weight: 600;
}

main{
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  width: 90%;
  max-width: 600px;
  height: auto;
  max-height: 600px;
  box-sizing: border-box;
  transition: .2s;
}

img{
  text-indent: -999999px; 
}

form{
  position: absolute;
  width: 100%;
  height: 100%;
}

.fullName{
  position: absolute;
  left: 63%;
  bottom: 50%;
  text-transform: uppercase;
  color: #FFF;
  pointer-events: none;
  font-size: 2.4em;
  line-height: 1.1em;
  width: 30%;
  display: table-cell;
  display: flex;
  justify-content: center;
  align-content: center;
  flex-direction: column;
}

.feat{
  position: absolute;
  left: 63%;
  top: 52%;
  text-transform: uppercase;
  color: #FFF;
  pointer-events: none;
  font-size: 1.5em;
  font-weight: 400;
  line-height: 1.3em;
  width: 28%;
  display: table-cell;
  display: flex;
  justify-content: center;
  align-content: center;
  flex-direction: column;
}

textarea.nameTextarea,
textarea.timeTextarea{
  position: absolute;
  left: 61%;
  bottom: 49%;
  height: auto;
  border:dashed 3px transparent;
  background:none;  
  outline: none;
  width: 30%;
  max-width: 30%;
  max-height: 35%;
  height: 21%;
  resize: none;
  color: transparent;
  text-align: left;  
  overflow: hidden;  
}

textarea.nameTextarea::selection,
textarea.timeTextarea::selection {
  background-color: transparent;
}

textarea.nameTextarea:invalid,
textarea.timeTextarea:invalid{
  border:dashed 3px #ff3b3b
}

textarea.nameTextarea:focus:invalid,
textarea.timeTextarea:focus:invalid{
  border:dashed 3px #ffe23b
}

textarea.nameTextarea:focus:required:valid,
textarea.timeTextarea:focus:required:valid{
  border:dashed 3px #52ff3b
}

textarea.nameTextarea:not(:focus):valid,
textarea.timeTextarea:not(:focus):valid{
  border:dashed 3px transparent;
}

textarea.timeTextarea{
  top: 56%;
  left: 77%;
  width: 14%;
  height: 5%;
  z-index: 9;
}

input[type="file"]{
  position: absolute;
  left: -12.9%;
  top: 9.7%; 
  border-radius: 500px;
  width: 69.3%;
  height: 69.3%;
  cursor: pointer;
  opacity: 0;
}

.avatar{
  background: url(/art-generator/static/media/avatarDefault.f878f512.png) center center no-repeat;
  background-size: cover;
  object-fit: cover;
  position: absolute;
  left: -12.9%;
  top: 9.7%; 
  border-radius: 500px;
  width: 69.3%;
  height: 69.3%;
  cursor: pointer;
  pointer-events: none;
}

.dashedAvatar{
  background-size: cover;
  object-fit: cover;
  position: absolute;
  left: -14%;
  top: 8%; 
  border-radius: 500px;
  width: 71%;
  height: 71%;
  cursor: pointer;
  pointer-events: none;
  border:dashed 3px #ff3b3b;
}

.nameTip,
.avatarTip,
.timeTip{
  position: absolute;
  background: #ffe23b;
  box-sizing: border-box;
  border-radius: 20px;
  font-size: .9em;
}

.nameTip:after,
.avatarTip:after,
.timeTip:after{
  content: '';
  display: block;
  width: 8px;
  height: 8px;
  position: absolute;
  left: 50%;
  bottom: 0;
  background: #ffe23b;
  transform: rotateZ(-45deg) translateX(-50%); 
}

.nameTip{
  left: 67%;
  bottom: 76%;
  padding: 2px 6px 6px 6px;
}

.avatarTip{
  left: 10%;
  top: 2%;
  padding: 2px 6px 6px 6px;
}

.timeTip:after{
  
  top: -6px
}

.timeTip{
  right: 4%;
  top: 68%;
  padding: 4px 6px;
}

input[type="file"]:valid ~ .dashedAvatar{
  border:none
}

input[type="file"]:valid ~ .avatarTip{
  display: none;
}

textarea.nameTextarea:not(:focus):valid ~ .nameTip{
  display: none;
}

textarea.timeTextarea:not(:focus):valid ~ .timeTip{
  display: none;
}  

.art{
  width: 100%;
}

.sArt{  
  position: relative;
  background: #000;
  overflow: hidden;
  width: 100%;
  height:100%;
  max-height: 600px;
}

@media only screen and (device-width:728px),
only screen and (max-width:728px) {
  main {
    width: 90%;
  }
  .feat{
    font-size: 3.4vw;
  }
  .fullName{
    font-size: 5vw;
  }
  .nameTip,
  .avatarTip,
  .timeTip{

    font-size: 2vw;
  }
}
